@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
*{margin:0;padding:0;outline:0;box-sizing:border-box}body{font:400 16px Roboto, sans-serif;background-color:#f0f0f5;-webkit-font-smoothing:antialiased}body input,body button{outline:none}

.content-calculator{display:flex;justify-content:center;align-items:center;height:100vh;flex-direction:column}.content-calculator h1{margin-bottom:16px}.content-calculator .calculator{height:320px;width:235px;background-color:gray;overflow:hidden;display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:1fr 48px 48px 48px 48px 48px}

.button{background-color:#f0f0f0;border-right:1px solid gray;border-left:1px solid gray;font-size:14px}.button:active{background-color:#ccc}.button.double{grid-column:span 2}.button.triple{grid-column:span 3}.button.operation{background-color:#fa8231;color:#fff}.button.operation:active{background-color:#fa8231cc}

.display{grid-column:span 4;background-color:rgba(0,0,0,0.5);display:flex;justify-content:flex-end;align-items:center;padding:20px;font-size:2.1em;color:#fff;overflow:hidden}

